import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          
  
  document.getElementById('root')
);
class Slideshow extends React.Component{
  constructor(props){
    super(props);
    this.state={slide:1};
  }
  click=()=> {
    this.setState({
      isDisabled: false
    });
  }
  kick=()=> {
    this.setState({
      isDisabled: true
    });
  
    setTimeout(
      function() {
        this.enableComponents()
      }.bind(this),
      3000
    );
  }
  auto=()=>{
    this.timer=setInterval(()=>{
      this.setState({slide:this.state.slide==5?1:this.state.slide +1}) 
    },3000)
  }
  next=()=>{
    this.setState({slide:this.state.slide==5?1:this.state.slide +1})
  }
  prev=()=>{
    this.setState({slide:this.state.slide==1?5:this.state.slide -1})
  }
  stop=()=>{
    clearInterval(this.timer);
  }
  render(){
    return(
      <div className='Slide'>
        <img className='Images' src={"images/"+"slide"+this.state.slide+".png" } alt="anh 1"/>
        <a className='button1' onClick={this.prev}><i className="fas fa-hand-point-left"></i></a>
        <a className='button'  onClick={this.next}><i className="fas fa-hand-point-right"></i></a>
        <div>
        <button type='button' className='button2' onClick={this.auto}>Auto</button>
        <button type='button'  className='button3' onClick={this.stop}>Stop</button>
        </div>
       </div>
    )
  }
    
}

ReactDOM.render(
  <Slideshow></Slideshow>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
